AhHI;EACE,gBAAA;AAkHN","sourcesContent":["@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@input-affix-with-clear-btn-width: 38px;\n\n// size mixins for input\n.input-lg() {\n  padding: @input-padding-vertical-lg @input-padding-horizontal-lg;\n  font-size: @font-size-lg;\n}\n\n.input-sm() {\n  padding: @input-padding-vertical-sm @input-padding-horizontal-sm;\n}\n\n// input status\n// == when focus or active\n.active(@borderColor: @primary-color; @hoverBorderColor: @primary-color-hover; @outlineColor: @primary-color-outline) {\n  & when (@theme = dark) {\n    border-color: @borderColor;\n  }\n  & when (not (@theme = dark) and not (@theme = variable)) {\n    border-color: @hoverBorderColor;\n  }\n  & when not (@theme = variable) {\n    box-shadow: @input-outline-offset @outline-blur-size @outline-width\n      fade(@borderColor, @outline-fade);\n  }\n  & when (@theme = variable) {\n    border-color: @hoverBorderColor;\n    box-shadow: @input-outline-offset @outline-blur-size @outline-width @outlineColor;\n  }\n  border-right-width: @border-width-base;\n  outline: 0;\n}\n\n// == when hover\n.hover(@color: @input-hover-border-color) {\n  border-color: @color;\n  border-right-width: @border-width-base;\n}\n\n.disabled() {\n  color: @input-disabled-color;\n  background-color: @input-disabled-bg;\n  border-color: @input-border-color;\n  box-shadow: none;\n  cursor: not-allowed;\n  opacity: 1;\n\n  &:hover {\n    .hover(@input-border-color);\n  }\n}\n\n// Basic style for input\n.input() {\n  position: relative;\n  display: inline-block;\n  width: 100%;\n  min-width: 0;\n  padding: @input-padding-vertical-base @input-padding-horizontal-base;\n  color: @input-color;\n  font-size: @font-size-base;\n  line-height: @line-height-base;\n  background-color: @input-bg;\n  background-image: none;\n  border: @border-width-base @border-style-base @input-border-color;\n  border-radius: @control-border-radius;\n  transition: all 0.3s;\n  .placeholder(); // Reset placeholder\n\n  &:hover {\n    .hover();\n  }\n\n  &:focus,\n  &-focused {\n    .active();\n  }\n\n  &-disabled {\n    .disabled();\n  }\n\n  &[disabled] {\n    .disabled();\n  }\n\n  &-borderless {\n    &,\n    &:hover,\n    &:focus,\n    &-focused,\n    &-disabled,\n    &[disabled] {\n      background-color: transparent;\n      border: none;\n      box-shadow: none;\n    }\n  }\n\n  // Reset height for `textarea`s\n  textarea& {\n    max-width: 100%; // prevent textearea resize from coming out of its container\n    height: auto;\n    min-height: @input-height-base;\n    line-height: @line-height-base;\n    vertical-align: bottom;\n    transition: all 0.3s, height 0s;\n  }\n\n  // Size\n  &-lg {\n    .input-lg();\n  }\n\n  &-sm {\n    .input-sm();\n  }\n}\n\n// label input\n.input-group(@inputClass) {\n  position: relative;\n  display: table;\n  width: 100%;\n  border-collapse: separate;\n  border-spacing: 0;\n\n  // Undo padding and float of grid classes\n  &[class*='col-'] {\n    float: none;\n    padding-right: 0;\n    padding-left: 0;\n  }\n\n  > [class*='col-'] {\n    padding-right: 8px;\n\n    &:last-child {\n      padding-right: 0;\n    }\n  }\n\n  &-addon,\n  &-wrap,\n  > .@{inputClass} {\n    display: table-cell;\n\n    &:not(:first-child):not(:last-child) {\n      border-radius: 0;\n    }\n  }\n\n  &-addon,\n  &-wrap {\n    width: 1px; // To make addon/wrap as small as possible\n    white-space: nowrap;\n    vertical-align: middle;\n  }\n\n  &-wrap > * {\n    display: block !important;\n  }\n\n  .@{inputClass} {\n    float: left;\n    width: 100%;\n    margin-bottom: 0;\n    text-align: inherit;\n\n    &:focus {\n      z-index: 1; // Fix https://gw.alipayobjects.com/zos/rmsportal/DHNpoqfMXSfrSnlZvhsJ.png\n      border-right-width: 1px;\n    }\n\n    &:hover {\n      z-index: 1;\n      border-right-width: 1px;\n      .@{ant-prefix}-input-search-with-button & {\n        z-index: 0;\n      }\n    }\n  }\n\n  &-addon {\n    position: relative;\n    padding